#app2{
}
#app2 .tab-bar{
    display: flex;
}
#app2 .tab-bar > li{
    border: 1px solid darkgray;
    width: 50%;
}
#app2 .tab-bar > li.selected{
    background: yellowgreen;
    color: aliceblue;
}
#app2 .tab-content{

}
#app2 .tab-content > li{
    display: none;
}
#app2 .tab-content > li.active{
    display: block;
}
